* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

#root {
  width: 100vw;
  height: 100vh;
  justify-content: baseline;
  align-items: center;
  flex-direction: column;
}

body {
  background-color: hsl(0, 0%, 20%);
  min-height: 100vh;
  display: flex;
}